@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Josefin sans', sans-serif;
}
.App{
  display: flex;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  position: relative;
}
.Menu{
  display: flex;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  position: relative;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: linear-gradient(to left, #FFAE00, #5B146f);  
  width: 100%;
  height:100vh;
}
.movie-bg{
  width: 100%;
  height: 100%;
  object-fit: fill;
  position: absolute;  
  filter: brightness(20%);
}
.movie-bg2{
  width: 100%;
  height: 100%;
  object-fit: fill;
  position: absolute;  
  filter: brightness(40%);
}
.about-movie{
  width: 80%;
  height: 20%;
  position: absolute;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.section1{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50vw;
  height: 100vh;
  position: relative;
  background-color: #e0e0e0;
}
.overlay{
  width: 80%;
  height: 80%;
  background: rgb(9,9,121);
  background: linear-gradient(177deg, rgba(9,9,121,0) 6%, rgba(0,0,0,0.8281729294478528) 100%);
  z-index: 33;
  position: absolute;
}
.section2{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50vw;
  height: 100vh;
  position: relative;
  transition: all 500ms linear;
}
.section3{
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #504f4f;
  width: 50vw;
  height: 100vh;
  position: absolute;
  right: 0;
}
.section2-animate{
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50vw;
  height: 100vh;
 animation: slideIn 500ms linear;
}
@keyframes slideIn {
  to{
    transform: translateX(-100%);
  }
}
@keyframes slideIn2 {
  to{
    transform: translateY(0);
  }
}
@keyframes slideIn {
  to{
    transform: translateX(-100%);
  }
}
.movie-title{
 font-size: 24px;
 margin: 30px 0;
}
.btn-wrapper{
  display: flex;
  flex-direction: column;
}
.btn{
  width: 150px;
  height: 50px;
  margin-bottom: 20px;
  background: none;
  color: #e0e0e0;
  border: 1px white solid;
  border-radius: 20px;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.btn:hover{
  color: #000;
  background-color: #fff;
}
.arrow-up {
  width: 0; 
  height: 0; 
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 8px solid rgb(255, 255, 255);
  margin: 5px;
}
.arrow-down {
  width: 0; 
  height: 0; 
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-top: 8px solid rgb(255, 255, 255);
  margin-left: 8px;
}
.btn:hover > .arrow-down{
  border-top: 8px solid rgb(0, 0, 0);
}
.btn:hover > .arrow-up{
  border-bottom: 8px solid rgb(0, 0, 0);
}
.box{
  height: 300px;
  width: 300px;
  background-color: #5e5d5d;
  margin: 40px;
}
.box-closed{
  display: none;
}
.lost-overlay{
  position: absolute;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, .8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 3;
}
.lost-box{
  height: 400px;
  width: 600px;
  background-color: #e0e0e0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.circle{
  position: absolute;
  height: 90px;
  width: 90px;
  border-radius: 50%;
  background-color: #ffffff;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  margin: auto;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
@keyframes SlideUp {
  from{
    transform: translateY(100%);
  }
  to{
    transform: translateY(0%);
  }
}
.counter{
  font-size: 3rem;
  margin-top: 20px;
}
.correct{
  height: 100px;
  width: 100px;
  background-color: aquamarine;
  position: absolute;
  animation: SlideUp .3s ease-in-out;
}
.wrong{
  height: 100px;
  width: 100px;
  background-color: rgb(213, 82, 17);
  position: absolute;
  animation: SlideUp .3s ease-in-out;
}
.answer-svg{
  width: 20px;
  z-index: 1;
}
.score{
  position: absolute;
  right: 20px;
  bottom: 40px;
  color: #fff;

}
.bollywood-logo{
  width: 300px;
}
.bollywood-bg{
  width: 1005;
  height: 100vh;
  position: absolute;
  z-index: -1;
  filter: brightness(40%);
}
.menu-card{
  width: 50vw;
  height: 70vh;
  background-color: rgba(255, 255, 255, .3);
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(10px);
  flex-direction: column;
  color: #fff;
  border-radius: 10px;
  border-top: 1px white solid;
  border-left: 1px white solid;
  text-align: center;
  padding: 0 20px;
}
.about-higher-lower{
  margin-top: 30px;
}
.circles{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.circles li{
  position: absolute;
  display: block;
  list-style: none;
  width: 20px;
  height: 20px;
  background: rgba(255, 255, 255, 0.2);
  animation: animate 25s linear infinite;
  bottom: -150px;
  
}

.circles li:nth-child(1){
  left: 25%;
  width: 80px;
  height: 80px;
  animation-delay: 0s;
}


.circles li:nth-child(2){
  left: 10%;
  width: 20px;
  height: 20px;
  animation-delay: 2s;
  animation-duration: 12s;
}

.circles li:nth-child(3){
  left: 70%;
  width: 20px;
  height: 20px;
  animation-delay: 4s;
}

.circles li:nth-child(4){
  left: 40%;
  width: 60px;
  height: 60px;
  animation-delay: 0s;
  animation-duration: 18s;
}

.circles li:nth-child(5){
  left: 65%;
  width: 20px;
  height: 20px;
  animation-delay: 0s;
}

.circles li:nth-child(6){
  left: 75%;
  width: 110px;
  height: 110px;
  animation-delay: 3s;
}

.circles li:nth-child(7){
  left: 35%;
  width: 150px;
  height: 150px;
  animation-delay: 7s;
}

.circles li:nth-child(8){
  left: 50%;
  width: 25px;
  height: 25px;
  animation-delay: 15s;
  animation-duration: 45s;
}

.circles li:nth-child(9){
  left: 20%;
  width: 15px;
  height: 15px;
  animation-delay: 2s;
  animation-duration: 35s;
}

.circles li:nth-child(10){
  left: 85%;
  width: 150px;
  height: 150px;
  animation-delay: 0s;
  animation-duration: 11s;
}



@keyframes animate {

  0%{
      transform: translateY(0) rotate(0deg);
      opacity: 1;
      border-radius: 0;
  }

  100%{
      transform: translateY(-1000px) rotate(720deg);
      opacity: 0;
      border-radius: 50%;
  }

}
.play-btn{
  height: 50px;
  width: 200px;
  margin-top: 40px;
  background: none;
  border: 1px white solid;
  border-radius: 20px;
  color: white;
  cursor: pointer;
}
.play-btn:hover{
  background-color: #fff;
  color: #000;
}
.note{
  margin-top: 40px;
}
.contact{
  display: flex;
  margin-top: 50px;
  bottom: 10px;
}
.icons{
  margin: 0 10px;
  cursor: pointer;
  filter: invert(100%);
}
.icons:hover{
  transform: scale(105%);  
}














