.App{
  flex-direction: column;
}

.section1{
  width: 100vw;
  height: 50vh;
}
.section2{
  width: 100vw;
  height: 50vh;
}
.section3{
  width: 100vw;
  height: 50vh;

}

.section2-animate{
  width: 100vw;
  height: 50vh;
 animation: slideIn 500ms linear;
}
@keyframes slideIn {
  to{
    transform: translateY(-100%);
  }
}
@keyframes slideIn2 {
